import React from "react";
import Head from "next/head";
import "../components/index.scss";
import App from "../components/App";

const Page = () => (
  <React.Fragment>
    <Head>
      <meta
        name="Description"
        content="Make a tailwind color palette in an intuitive way"
      />
      <title>Make a tailwind color palette</title>
    </Head>
    <App
      title={
        <h1 className="display-4 mb-4 text-center">
          <strong>
            Make a <span style={{ color: "#563d7c" }}>Tailwind</span> color
            palette
          </strong>{" "}
          for your theme
        </h1>
      }
      initialState={{
        hues: [
          { name: "red", value: 2 }, // hsl(2°, 76%, 54%)
          { name: "orange", value: 30 }, // hsl(30°, 91%, 61%)
          { name: "yellow", value: 54 }, // hsl(54°, 100%, 65%)
          { name: "green", value: 145 }, // hsl(145°, 55%, 49%)
          { name: "teal", value: 174 }, // hsl(174°, 48%, 53%)
          { name: "blue", value: 207 }, // hsl(207°, 71%, 53%)
          { name: "indigo", value: 231 }, // hsl(231°, 51%, 60%)
          { name: "purple", value: 264 }, // hsl(264°, 69%, 63%)
          { name: "pink", value: 340 } // hsl(340°, 88%, 70%)
        ],
        lightnesses: [
          { name: "darkest", value: 98, domain: [25, 75] },
          { name: "darker", value: 82, domain: [25, 75] },
          { name: "dark", value: 66, domain: [25, 75] },
          { name: "base", value: 50, domain: [25, 75] },
          { name: "light", value: 34, domain: [25, 75] },
          { name: "lighter", value: 18, domain: [25, 75] },
          { name: "lightest", value: 2, domain: [25, 75] }
        ],
        saturation: 80,
        exportType: "JS"
      }}
      exportsTypes={[
        {
          name: "JS",
          toString: ({ hues, lightnesses, saturation, hsl }) =>
            hues
              .map(hue => `$${hue.name}: ${hsl(hue.name, "base")};`)
              .join("\n") +
            `

var colors = {
    'transparent': 'transparent',

    'black': '#222b2f',
    'grey-darkest': '#364349',
    'grey-darker': '#596a73',
    'grey-dark': '#70818a',
    'grey': '#9babb4',

    // ...
    }
            `
        }
      ]}
      columnClass="col-lg-1 col-md-3 col-6"
    />
  </React.Fragment>
);

export default Page;
